import React from "react";
import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import IconButton from "@material-ui/core/IconButton";
import InputAdornment from "@material-ui/core/InputAdornment";
import TextField from "@material-ui/core/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";

import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";

import { Link } from "react-router";
import Icon from "@material-ui/core/Icon";

import Button from '@material-ui/core/Button';

const ranges = [
  {
    value: "0-20",
    label: "0 to 20"
  },
  {
    value: "21-50",
    label: "21 to 50"
  },
  {
    value: "51-100",
    label: "51 to 100"
  }
];

const useStyles = makeStyles(theme => ({
  root: {
    display: "flex",
    flexWrap: "wrap"
  },
  margin: {
    margin: theme.spacing(1)
  },
  textField: {
    width:'100%'
  },
  button: {
    width:'100%',
    "text-decoration": "none"
  }
}));

export default function OutlinedInputAdornments() {
  const classes = useStyles();
  const [values, setValues] = React.useState({
    amount: "",
    password: "",
    weight: "",
    weightRange: "",
    showPassword: false
  });

  const handleChange = prop => event => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };

  return (
    <div className={classes.root}>
      <AppBar position="static" className="App-main-bg-color">
        <Toolbar>
          <Typography variant="h6" color="inherit">
            <Link to="/">{<Icon className='App-backspace'>keyboard_backspace</Icon>}</Link>
            Connester
          </Typography>
        </Toolbar>
      </AppBar>


       
      <TextField
        id="outlined-simple-start-adornment"
        className={clsx(classes.margin, classes.textField)}
        variant="outlined"
        label="Your Full Name(** as per application)"
        InputProps={{
          startAdornment: <InputAdornment position="start"></InputAdornment>
        }}
      />
      <TextField
        select
        className={clsx(classes.margin, classes.textField)}
        variant="outlined"
        label="Max Qualification (**as per application)"
        value={values.weightRange}
        onChange={handleChange("weightRange")}
        InputProps={{
          startAdornment: <InputAdornment position="start"></InputAdornment>
        }}
      >
        {ranges.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
      <TextField
        id="outlined-adornment-amount"
        className={clsx(classes.margin, classes.textField)}
        variant="outlined"
        label="Membership Type (**account type)"
        value={values.amount}
        onChange={handleChange("amount")}
        InputProps={{
          startAdornment: <InputAdornment position="start">$</InputAdornment>
        }}
      />
      <Link to='/signin' className={classes.button}><Button  variant="contained" color="secondary" className={classes.button}>
        Sign Out
      </Button></Link>
    </div>
  );
}
