import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import Icon from '@material-ui/core/Icon';
import RestoreIcon from '@material-ui/icons/Restore';
import HomeIcon from '@material-ui/icons/Home';
import FavoriteIcon from '@material-ui/icons/Favorite';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';


import { Link } from 'react-router'

import Posts from './posts'

const useStyles = makeStyles({
  header:{
    background: "#2196f3",
    "margin-bottom": "30px",
    position: "fixed"
  },
  root: {
    width: '100%'
  },
  footer:{
    width: '100%',
    position:"fixed",
    bottom:'0%'
  },footer_space:{
    height:"65px"
  },linkstyle:{
    "text-decoration":"blink"
  }
});

export default function LabelBottomNavigation() {
  const classes = useStyles();
  const [value, setValue] = React.useState('Home');

  function handleChange(event, newValue) {
    setValue(newValue);
  }

  return (
    <div className={classes.root}>
    <AppBar position="static" className={classes.header}>
      <Toolbar>
        <Typography variant="h6" color="inherit">
          Connester
        </Typography>
      </Toolbar>
    </AppBar>

    <div className={classes.footer_space}></div>
    <Posts></Posts>
    <div className={classes.footer_space}></div>
    
    <BottomNavigation value={value} onChange={handleChange} className={classes.footer}>
    <BottomNavigationAction label="Home" value="Home" icon={<HomeIcon />} />
    <BottomNavigationAction label="Recents" value="recents" icon={<RestoreIcon />} />
      <BottomNavigationAction label="Applied" value="applied" icon={<FavoriteIcon />} />
      <Link to='/search'  className={classes.linkstyle}><BottomNavigationAction label="Search" value="Search" icon={<Icon>search</Icon>} /></Link>
      <Link to='/profile'><BottomNavigationAction label="Profile" value="Profile" icon={<Icon>person outline</Icon>} /></Link>
    </BottomNavigation>

  </div>

  );
}
